import React, { useState, useEffect } from 'react';
import { format, addDays, subDays, isSameDay, isToday, isPast, isFuture } from 'date-fns';
import './Calendar.scss';

export default function Calendar() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [goals, setGoals] = useState(() => {
    const saved = localStorage.getItem('calendar-goals');
    return saved ? JSON.parse(saved) : {};
  });
  const [newTask, setNewTask] = useState('');
  const [showTaskInput, setShowTaskInput] = useState(false);
  const [viewMode, setViewMode] = useState('month'); // 'month' or 'week'

  useEffect(() => {
    localStorage.setItem('calendar-goals', JSON.stringify(goals));
  }, [goals]);

  const generateMonthDays = () => {
    const days = [];
    const startDate = new Date(currentDate);
    startDate.setDate(1);
    
    while (startDate.getDay() !== 1) {
      startDate.setDate(startDate.getDate() - 1);
    }

    for (let i = 0; i < 42; i++) {
      days.push(new Date(startDate));
      startDate.setDate(startDate.getDate() + 1);
    }

    return days;
  };

  const generateWeekDays = () => {
    const days = [];
    const startDate = new Date(currentDate);
    
    // Find previous Monday
    while (startDate.getDay() !== 1) {
      startDate.setDate(startDate.getDate() - 1);
    }

    for (let i = 0; i < 7; i++) {
      days.push(new Date(startDate));
      startDate.setDate(startDate.getDate() + 1);
    }

    return days;
  };

  const renderDays = () => {
    const days = viewMode === 'month' ? generateMonthDays() : generateWeekDays();
    
    return (
      <div className={`calendar-grid ${viewMode}`}>
        {['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'].map(day => (
          <div key={day} className="weekday-header">
            <span>{day.substring(0, viewMode === 'month' ? 3 : 3)}</span>
          </div>
        ))}
        
        {days.map((day) => {
          const dateStr = day.toISOString().split('T')[0];
          const dayGoals = goals[dateStr] || { tasks: [], note: '' };
          const isCurrentDay = isSameDay(day, currentDate);
          const isDayToday = isToday(day);
          const isDayPast = isPast(day) && !isDayToday;
          const isDayFuture = isFuture(day) && !isDayToday;
          const hasTasks = dayGoals.tasks.length > 0;
          const completedTasks = dayGoals.tasks.filter(t => t.completed).length;

          return (
            <div 
              key={dateStr} 
              className={`day-cell 
                ${isCurrentDay ? 'current-day' : ''} 
                ${isDayToday ? 'today' : ''}
                ${isDayPast ? 'past-day' : ''}
                ${isDayFuture ? 'future-day' : ''}
                ${hasTasks ? 'has-tasks' : ''}`}
              onClick={() => {
                setCurrentDate(day);

              }}
            >
              <div className="day-header">
                <div className="day-number">{format(day, 'd')}</div>
                {isDayToday && <div className="today-badge">Today</div>}
              </div>
              
              <div className="day-content">
                {hasTasks && (
                  <div className="task-progress">
                    <div className="progress-bar" style={{ width: `${(completedTasks / dayGoals.tasks.length) * 100}%` }}></div>
                    <span>{completedTasks}/{dayGoals.tasks.length}</span>
                  </div>
                )}
                
                {viewMode === 'month' && (
                  <div className="tasks-preview">
                    {dayGoals.tasks.slice(0, 2).map((task, index) => (
                      <div 
                        key={index} 
                        className={`task-preview ${task.completed ? 'completed' : ''}`}
                      >
                        <div className="task-preview-check"></div>
                        {task.text.substring(0, 12)}{task.text.length > 12 ? '...' : ''}
                      </div>
                    ))}
                  </div>
                )}
                
                {viewMode === 'week' && (
                  <div className="week-view-tasks">
                    {dayGoals.tasks.map((task, index) => (
                      <div 
                        key={index} 
                        className={`week-task ${task.completed ? 'completed' : ''}`}
                        onClick={(e) => {
                          e.stopPropagation();
                          toggleTask(dateStr, index);
                        }}
                      >
                        {task.text}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          );
        })}
      </div>
    );
  };

  const exportData = () => {
    const dataStr = JSON.stringify(goals);
    const dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
    
    const exportFileDefaultName = `calendar-data-${format(new Date(), 'yyyy-MM-dd')}.json`;
    
    const linkElement = document.createElement('a');
    linkElement.setAttribute('href', dataUri);
    linkElement.setAttribute('download', exportFileDefaultName);
    linkElement.click();
  };

  const importData = (event) => {
    const file = event.target.files[0];
    if (!file) return;
    
    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const importedGoals = JSON.parse(e.target.result);
        if (typeof importedGoals === 'object' && importedGoals !== null) {
          setGoals(importedGoals);
          alert('Data imported successfully!');
        } else {
          alert('Invalid data format. Please import a valid JSON file.');
        }
      } catch (error) {
        alert('Error parsing the file. Please make sure it contains valid JSON data.');
        console.error('Import error:', error);
      }
    };
    reader.readAsText(file);
    
    // Reset the input so the same file can be imported again if needed
    event.target.value = '';
  };

  const renderSelectedDay = () => {
    const dateStr = currentDate.toISOString().split('T')[0];
    const dayGoals = goals[dateStr] || { tasks: [], note: '' };
    const completedCount = dayGoals.tasks.filter(t => t.completed).length;
    const progressPercentage = dayGoals.tasks.length > 0 
      ? (completedCount / dayGoals.tasks.length) * 100 
      : 0;

    return (
      <div className="selected-day-view">
        <div className="day-header">
          <h2 className="selected-day-title">
            {format(currentDate, 'EEEE, MMMM d')}
            <span className="year">{format(currentDate, 'yyyy')}</span>
          </h2>
          <div className="day-actions">
            <button 
              className="action-button add-task-button"
              onClick={() => setShowTaskInput(!showTaskInput)}
            >
              <span className="icon">+</span> Add Task
            </button>
          </div>
        </div>
        
        <div className="progress-container">
          <div className="progress-bar" style={{ width: `${progressPercentage}%` }}></div>
          <div className="progress-text">
            {completedCount} of {dayGoals.tasks.length} tasks completed
          </div>
        </div>
        
        {showTaskInput && (
          <div className="task-input-container">
            <input
              type="text"
              value={newTask}
              onChange={(e) => setNewTask(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && addTask(dateStr)}
              placeholder="What needs to be done?"
              className="task-input"
              autoFocus
            />
            <div className="input-actions">
              <button 
                className="cancel-button"
                onClick={() => {
                  setShowTaskInput(false);
                  setNewTask('');
                }}
              >
                Cancel
              </button>
              <button 
                className="confirm-button"
                onClick={() => addTask(dateStr)}
                disabled={!newTask.trim()}
              >
                Add Task
              </button>
            </div>
          </div>
        )}
        
        <div className="tasks-container">
          {dayGoals.tasks.length > 0 ? (
            dayGoals.tasks.map((task, index) => (
              <div key={index} className="task-item">
                <div className="task-main">
                  <button
                    className={`task-check ${task.completed ? 'completed' : ''}`}
                    onClick={() => toggleTask(dateStr, index)}
                    aria-label={task.completed ? 'Mark incomplete' : 'Mark complete'}
                  >
                    {task.completed && (
                      <svg viewBox="0 0 24 24" width="16" height="16">
                        <path fill="currentColor" d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41L9 16.17z"/>
                      </svg>
                    )}
                  </button>
                  <span className={`task-text ${task.completed ? 'completed' : ''}`}>
                    {task.text}
                  </span>
                </div>
                <button 
                  className="task-delete"
                  onClick={() => deleteTask(dateStr, index)}
                  aria-label="Delete task"
                >
                  <svg viewBox="0 0 24 24" width="18" height="18">
                    <path fill="currentColor" d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/>
                  </svg>
                </button>
              </div>
            ))
          ) : (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h3>No tasks for today</h3>
            </div>
          )}
        </div>
        
        <div className="day-notes">
          <h3 className="notes-title">
            <svg viewBox="0 0 24 24" width="20" height="20">
              <path fill="currentColor" d="M19 3h-4.18C14.4 1.84 13.3 1 12 1c-1.3 0-2.4.84-2.82 2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm-7 0c.55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1 .45-1 1-1zm2 14H7v-2h7v2zm3-4H7v-2h10v2zm0-4H7V7h10v2z"/>
            </svg>
            Daily Notes
          </h3>
          <textarea
            value={dayGoals.note || ''}
            onChange={(e) => updateNote(dateStr, e.target.value)}
            placeholder="Jot down your thoughts, ideas, or reminders..."
            className="notes-textarea"
          />
          <div className="notes-footer">
            <span className="character-count">{dayGoals.note ? dayGoals.note.length : 0}/500</span>
            <button className="save-notes-button">
              Save Notes
            </button>
          </div>
        </div>
      </div>
    );
  };

  const addTask = (date) => {
    if (!newTask.trim()) return;
    
    setGoals(prev => ({
      ...prev,
      [date]: {
        ...(prev[date] || { note: '' }),
        tasks: [...(prev[date]?.tasks || []), { text: newTask, completed: false }]
      }
    }));
    setNewTask('');
    setShowTaskInput(false);
  };

  const toggleTask = (date, index) => {
    setGoals(prev => ({
      ...prev,
      [date]: {
        ...prev[date],
        tasks: prev[date].tasks.map((task, i) => 
          i === index ? { ...task, completed: !task.completed } : task
        )
      }
    }));
  };

  const deleteTask = (date, index) => {
    setGoals(prev => ({
      ...prev,
      [date]: {
        ...prev[date],
        tasks: prev[date].tasks.filter((_, i) => i !== index)
      }
    }));
  };

  const updateNote = (date, note) => {
    setGoals(prev => ({
      ...prev,
      [date]: {
        ...(prev[date] || { tasks: [] }),
        note: note.substring(0, 500)
      }
    }));
  };

  const navigatePeriod = (direction) => {
    setCurrentDate(prev => {
      const newDate = new Date(prev);
      if (viewMode === 'month') {
        newDate.setMonth(direction === 'next' ? prev.getMonth() + 1 : prev.getMonth() - 1);
      } else {
        newDate.setDate(direction === 'next' ? prev.getDate() + 7 : prev.getDate() - 7);
      }
      return newDate;
    });
  };

  const goToToday = () => {
    setCurrentDate(new Date());
    setViewMode('month');
  };

  return (
    <div className="calendar-app">
      <div className="calendar-header">
        <div className="header-left">

          <button className="today-button" onClick={goToToday}>
            Today
          </button>
        </div>
        
        <div className="header-center">
          <button 
            className="nav-button prev-button"
            onClick={() => navigatePeriod('prev')}
          >
            <svg viewBox="0 0 24 24" width="20" height="20">
              <path fill="currentColor" d="M15.41 16.59L10.83 12l4.58-4.59L14 6l-6 6 6 6 1.41-1.41z"/>
            </svg>
          </button>
          
          <h2 className="period-title">
            {viewMode === 'month' 
              ? format(currentDate, 'MMMM yyyy') 
              : `${format(currentDate, 'MMM d')} - ${format(addDays(currentDate, 6), 'MMM d, yyyy')}`
            }
          </h2>
          
          <button 
            className="nav-button next-button"
            onClick={() => navigatePeriod('next')}
          >
            <svg viewBox="0 0 24 24" width="20" height="20">
              <path fill="currentColor" d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z"/>
            </svg>
          </button>
        </div>
        
        <div className="header-right">
          <div className="view-toggle">
            <button 
              className={`toggle-option ${viewMode === 'month' ? 'active' : ''}`}
              onClick={() => setViewMode('month')}
            >
              Month
            </button>
            <button 
              className={`toggle-option ${viewMode === 'week' ? 'active' : ''}`}
              onClick={() => setViewMode('week')}
            >
              Week
            </button>
          </div>
        </div>
      </div>
      
      <div className="calendar-container">
        {renderDays()}
      </div>
      
      <div className="day-detail-container">
        {renderSelectedDay()}
      </div>

      <div className="data-actions">
            <button className="export-button" onClick={exportData}>
              Export to JSON
            </button>
            <label htmlFor="import-data" className="import-button">
              Import from JSON
              <input
                id="import-data"
                type="file"
                accept=".json"
                onChange={importData}
                style={{ display: 'none' }}
              />
            </label>
            
          </div>
    </div>
  );
}