import React, { useState, useEffect, useRef, useCallback } from 'react';
import { format, subDays, addDays, isToday, parse } from 'date-fns';
import './DailyEntry.scss';
import { loadData, saveData } from '../services/dataService';

const initialState = {
  date: new Date(),
  efficiency: null,
  productivity: null,
  happiness: null,
  pomodoros: 0,
  pomodorosHistory: {
    '30': { count: 0, color: '#10b981' },
    '45': { count: 0, color: '#f59e0b' },
    '60': { count: 0, color: '#dc2626' }
  },
  energy: {
    air: null,
    fire: null,
    water: null,
    earth: null,
  },
  victory: '',
  loss: '',
  insight: '',
  activePomodoro: null,
  pomodoroDuration: 30,
  todos: [
    { id: 1, text: '', completed: false },
    { id: 2, text: '', completed: false },
    { id: 3, text: '', completed: false },
  ]
};

export default function DailyEntry() {
  const [entry, setEntry] = useState(initialState);
  const [history, setHistory] = useState([]);
  const [timer, setTimer] = useState(null);
  const [isPomodoroActive, setIsPomodoroActive] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const audioRef = useRef(null);

  const navigateDate = (direction) => {
    const newDate = direction === 'prev' 
      ? subDays(entry.date, 1) 
      : addDays(entry.date, 1);
    
    loadEntryForDate(newDate);
  };

  const loadEntryForDate = (date) => {
    const dateString = format(date, 'dd/MM/yyyy');
    const foundEntry = history.find(e => e.date === dateString);
    
    if (foundEntry) {
      setEntry({
        ...foundEntry,
        date: parse(foundEntry.date, 'dd/MM/yyyy', new Date())
      });
    } else {
      const lastEntry = history[history.length - 1];
      
      setEntry({
        ...initialState,
        date: date,
        efficiency: lastEntry?.efficiency || null,
        productivity: lastEntry?.productivity || null,
        happiness: lastEntry?.happiness || null,
        energy: lastEntry?.energy || { ...initialState.energy },
        pomodorosHistory: lastEntry?.pomodorosHistory || { ...initialState.pomodorosHistory },
      });
    }
  };

  const handlePomodoroEnd = useCallback(() => {
    if (!isPomodoroActive) return;

    const duration = entry.pomodoroDuration.toString();
    
    setEntry(prev => {
      const newCount = (prev.pomodorosHistory[duration]?.count || 0) + 1;
      return {
        ...prev,
        pomodoros: (prev.pomodoros || 0) + 1,
        pomodorosHistory: {
          ...prev.pomodorosHistory,
          [duration]: {
            ...prev.pomodorosHistory[duration],
            count: newCount
          }
        }
      };
    });
  
    setIsPomodoroActive(false);
    setTimer(null);
  
    if (!isMuted && audioRef.current) {
      audioRef.current.currentTime = 0;
      audioRef.current.play().catch(error => {
        console.error("Audio playback failed:", error);
        document.addEventListener('click', () => {
          audioRef.current.play().catch(e => console.error("Retry failed:", e));
        }, { once: true });
      });
    }
  }, [entry.pomodoroDuration, isMuted, isPomodoroActive]);

  useEffect(() => {
    const saved = loadData();
    setHistory(saved);
    
    const today = new Date();
    const todayString = format(today, 'dd/MM/yyyy');
    const todayEntry = saved.find(e => e.date === todayString);

    if (todayEntry) {
      setEntry({
        ...todayEntry,
        date: parse(todayEntry.date, 'dd/MM/yyyy', new Date())
      });
    } else {
      const yesterdayString = format(subDays(today, 1), 'dd/MM/yyyy');
      const yesterdayEntry = saved.find(e => e.date === yesterdayString);
      
      setEntry({
        ...initialState,
        date: today,
        efficiency: yesterdayEntry?.efficiency || null,
        productivity: yesterdayEntry?.productivity || null,
        happiness: yesterdayEntry?.happiness || null,
        energy: yesterdayEntry?.energy || { ...initialState.energy },
      });
    }

    audioRef.current = new Audio(`${process.env.PUBLIC_URL}/timer_music.mp3`);
    audioRef.current.load();

    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current = null;
      }
    };
  }, []);

  useEffect(() => {
    let countdown;
    
    if (timer !== null && timer > 0) {
      countdown = setInterval(() => {
        setTimer(prev => {
          if (prev <= 1) {
            clearInterval(countdown);
            handlePomodoroEnd();
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
    }
  
    return () => {
      if (countdown) clearInterval(countdown);
    };
  }, [timer, isPomodoroActive, handlePomodoroEnd]);

  const handleChange = (key, value) => {
    if (key === 'pomodorosHistory') {
      setEntry(prev => ({
        ...prev,
        pomodorosHistory: value,
        pomodoros: Object.values(value).reduce((sum, { count }) => sum + (count || 0), 0)
      }));
    } else {
      setEntry(prev => ({ ...prev, [key]: value }));
    }
  };

  const handleEnergy = (key, value) => {
    setEntry(prev => ({
      ...prev,
      energy: { ...prev.energy, [key]: value }
    }));
  };

  const saveEntry = () => {
    if (entry.efficiency === null || entry.productivity === null || entry.happiness === null) {
      alert('Please fill all metrics (efficiency, productivity, happiness)');
      return;
    }
  
    if (!entry.victory.trim()) {
      alert('Victories field is required! Please note at least one positive thing from your day.');
      return;
    }

    const entryToSave = {
      ...entry,
      date: format(entry.date, 'dd/MM/yyyy')
    };
  
    const existingIndex = history.findIndex(e => e.date === entryToSave.date);
    const updated = [...history];
  
    if (existingIndex >= 0) {
      updated[existingIndex] = entryToSave;
      alert('Entry updated successfully!');
    } else {
      updated.push(entryToSave);
      alert('New entry saved successfully!');
    }
  
    if (saveData(updated)) {
      setHistory(updated);
    }
  };

  const getMetricColor = (value) => {
    if (value === null) return '#94a3b8';
    if (value < 33) return '#dc2626';
    if (value < 66) return '#f59e0b';
    return '#10b981';
  };

  const startPomodoro = (duration) => {
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
    }
    
    setIsPomodoroActive(true);
    setEntry(prev => ({
      ...prev,
      pomodoroDuration: duration
    }));
    setTimer(duration * 60);
  };

  const toggleMute = () => {
    if (audioRef.current) {
      if (isMuted) {
        audioRef.current.play().catch(e => console.error("Audio play failed:", e));
      } else {
        audioRef.current.pause();
      }
    }
    setIsMuted(!isMuted);
  };

  const cancelPomodoro = () => {
    setIsPomodoroActive(false);
    setTimer(null);
    if (audioRef.current) {
      audioRef.current.pause();
    }
  };

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const handleTodoChange = (id, key, value) => {
    setEntry(prev => ({
      ...prev,
      todos: prev.todos.map(todo => 
        todo.id === id ? { ...todo, [key]: value } : todo
      )
    }));
  };
  const deleteTodo = (id) => {
    setEntry(prev => ({
      ...prev,
      todos: prev.todos.filter(todo => todo.id !== id)
    }));
  };

  
  return (
    <div className="daily-entry">
      <div className="card">
        <div className="section-box date-section">
          <h2 className="section-title">Daily Journal</h2>
          <div className="date-container">
            <button 
              className="date-nav-button prev" 
              onClick={() => navigateDate('prev')}
            >
              
            </button>

            <div className="date-display">
              {format(entry.date, 'EEEE, MMMM do')}
              {isToday(entry.date) && <span className="today-badge">Today</span>}
              {entry.date > new Date() && <span className="future-badge">Future</span>}
            </div>
            
            <button 
              className="date-nav-button next" 
              onClick={() => navigateDate('next')}
            >
              
            </button>
          </div>
        </div>

        <div className="bottom-section">
          <div className="section-box metrics-section">
            <h3 className="section-subtitle">Performance Metrics</h3>
            <div className="metrics-grid">
              {['efficiency', 'productivity', 'happiness'].map(metric => (
                <div key={metric} className="metric-card">
                  <label className="metric-label">
                    {metric.charAt(0).toUpperCase() + metric.slice(1)}
                    <span className="metric-value" style={{ color: getMetricColor(entry[metric]) }}>
                      {entry[metric] ?? ''}%
                    </span>
                  </label>
                  <input 
                    type="range" 
                    min="0" 
                    max="100" 
                    value={entry[metric] ?? 50} 
                    onChange={e => handleChange(metric, +e.target.value)} 
                    className="metric-slider"
                    style={{ '--track-color': getMetricColor(entry[metric]) }}
                  />
                  <div className="slider-labels">
                    <span>0</span>
                    <span>50</span>
                    <span>100</span>
                  </div>
                </div>
              ))}
              
              <div className="metric-card pomodoros-card">
                <label className="input-label">Pomodoros Completed</label>
                <div className="pomodoro-counters">
                  <div className="pomodoro-counter green">
                    <span className="pomodoro-label">30m</span>
                    <div className="counter-controls">
                      <button 
                        className="counter-button" 
                        onClick={() => handleChange('pomodorosHistory', {
                          ...entry.pomodorosHistory,
                          '30': {
                            ...entry.pomodorosHistory['30'],
                            count: Math.max(0, (entry.pomodorosHistory['30']?.count || 0) - 1)
                          }
                        })}
                      >
                        -
                      </button>
                      <span className="pomodoro-count">
                        {entry.pomodorosHistory?.['30']?.count || 0}
                      </span>
                      <button 
                        className="counter-button" 
                        onClick={() => handleChange('pomodorosHistory', {
                          ...entry.pomodorosHistory,
                          '30': {
                            ...entry.pomodorosHistory['30'],
                            count: (entry.pomodorosHistory['30']?.count || 0) + 1
                          }
                        })}
                      >
                        +
                      </button>
                    </div>
                  </div>

                  <div className="pomodoro-counter yellow">
                    <span className="pomodoro-label">45m</span>
                    <div className="counter-controls">
                      <button 
                        className="counter-button" 
                        onClick={() => handleChange('pomodorosHistory', {
                          ...entry.pomodorosHistory,
                          '45': {
                            ...entry.pomodorosHistory['45'],
                            count: Math.max(0, (entry.pomodorosHistory['45']?.count || 0) - 1)
                          }
                        })}
                      >
                        -
                      </button>
                      <span className="pomodoro-count">
                        {entry.pomodorosHistory?.['45']?.count || 0}
                      </span>
                      <button 
                        className="counter-button" 
                        onClick={() => handleChange('pomodorosHistory', {
                          ...entry.pomodorosHistory,
                          '45': {
                            ...entry.pomodorosHistory['45'],
                            count: (entry.pomodorosHistory['45']?.count || 0) + 1
                          }
                        })}
                      >
                        +
                      </button>
                    </div>
                  </div>

                  <div className="pomodoro-counter red">
                    <span className="pomodoro-label">60m</span>
                    <div className="counter-controls">
                      <button 
                        className="counter-button" 
                        onClick={() => handleChange('pomodorosHistory', {
                          ...entry.pomodorosHistory,
                          '60': {
                            ...entry.pomodorosHistory['60'],
                            count: Math.max(0, (entry.pomodorosHistory['60']?.count || 0) - 1)
                          }
                        })}
                      >
                        -
                      </button>
                      <span className="pomodoro-count">
                        {entry.pomodorosHistory?.['60']?.count || 0}
                      </span>
                      <button 
                        className="counter-button" 
                        onClick={() => handleChange('pomodorosHistory', {
                          ...entry.pomodorosHistory,
                          '60': {
                            ...entry.pomodorosHistory['60'],
                            count: (entry.pomodorosHistory['60']?.count || 0) + 1
                          }
                        })}
                      >
                        +
                      </button>
                    </div>
                  </div>
                </div>

                <div className="pomodoro-timer-container">
                  {isPomodoroActive ? (
                    <div className="pomodoro-active">
                      <div className="timer-display">
                        <span className="time">{formatTime(timer)}</span>
                        <span className="label">remaining</span>
                      </div>
                      <div className="pomodoro-controls">
                        <button className="cancel-button" onClick={cancelPomodoro}>
                          Cancel
                        </button>
                        {/*<button 
                          className={`mute-button ${isMuted ? 'muted' : ''}`} 
                          onClick={toggleMute}
                        >
                          {isMuted ? '' : ''}
                        </button>*/}
                      </div>
                    </div>
                  ) : (
                    <div className="pomodoro-setup">
                      <h4 className="pomodoro-title">Start Pomodoro Session</h4>
                      <div className="duration-buttons">
                        <button 
                          className="duration-button green" 
                          onClick={() => startPomodoro(30)}
                          style={{ backgroundColor: '#10b981' }}
                        >
                          30 min
                        </button>
                        <button 
                          className="duration-button yellow" 
                          onClick={() => startPomodoro(45)}
                          style={{ backgroundColor: '#f59e0b' }}
                        >
                          45 min
                        </button>
                        <button 
                          className="duration-button red" 
                          onClick={() => startPomodoro(60)}
                          style={{ backgroundColor: '#dc2626' }}
                        >
                          60 min
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>

          <div className="section-box energy-section">
            <h3 className="section-subtitle">Energy Levels</h3>
            <div className="energy-grid">
              {Object.entries(entry.energy).map(([key, value]) => (
                <div key={key} className={`energy-card energy-${key}`}>
                  <label className="energy-label">
                    {key.charAt(0).toUpperCase() + key.slice(1)}
                    <span className="energy-value">{value ?? ''}%</span>
                  </label>
                  <div className="energy-slider-container">
                    <input 
                      type="range" 
                      min="0" 
                      max="100" 
                      value={value ?? 50} 
                      onChange={e => handleEnergy(key, +e.target.value)}
                      className="energy-slider"
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="section-box insights-section">
            <h3 className="section-subtitle">Daily Reflections</h3>
            <div className="insights-grid">
              <div className="insight-card victory-card">
                <label className="input-label">  Victories / Losses <span className="required">*</span></label>
                <textarea 
                  value={entry.victory} 
                  onChange={e => handleChange('victory', e.target.value)} 
                  placeholder="What went well or not so much today?"
                  className="insight-textarea"
                  rows="4"
                  required
                />
              </div>
              <div className="insight-card loss-card">
                <label className="input-label"> Dreams</label>
                <textarea 
                  value={entry.loss} 
                  onChange={e => handleChange('loss', e.target.value)} 
                  placeholder="Do those dreams want tell you something?"
                  className="insight-textarea"
                  rows="4"
                />
              </div>
              <div className="insight-card insight-card">
                <label className="input-label"> Insights</label>
                <textarea 
                  value={entry.insight} 
                  onChange={e => handleChange('insight', e.target.value)} 
                  placeholder="Did you learn something today?"
                  className="insight-textarea"
                  rows="4"
                />
              </div>
            </div>
          </div>
          <div className="section-box todos-section">
            <h3 className="section-subtitle">Daily Tasks</h3>
            <div className="todos-grid">
    {(entry.todos || []).map((todo) => (
      <div 
        key={todo.id} 
        className={`todo-card ${todo.completed ? 'completed' : ''}`}
      >
        <div className="todo-controls">
          <input
            type="checkbox"
            checked={todo.completed}
            onChange={(e) => handleTodoChange(todo.id, 'completed', e.target.checked)}
            className="todo-checkbox"
          />
          <textarea
            value={todo.text}
            onChange={(e) => handleTodoChange(todo.id, 'text', e.target.value)}
            placeholder="Enter a task..."
            className="todo-input"
            rows={Math.max(1, Math.floor(todo.text.length / 30))}
          />
          <button 
            className="delete-todo-button"
            onClick={() => deleteTodo(todo.id)}
          >
            
          </button>
        </div>
      </div>
    ))}
    <button 
      className="add-todo-button"
      onClick={() => {
        setEntry(prev => ({
          ...prev,
          todos: [
            ...(prev.todos || []),
            { id: Date.now(), text: '', completed: false }
          ]
        }));
      }}
    >
      Add Task
    </button>
  </div>
          </div>
        </div>

        <button className="save-button" onClick={saveEntry}>
          Save Daily Entry
        </button>
      </div>
    </div>
  );
}